.pageGrid {
    display: grid;
    height: 100vh;
    width: 99vw;
    grid-template-columns: 3% auto 3%;
    grid-template-rows: 100px auto 100px;
    row-gap: 1em;
    grid-template-areas:
        ". NavBar ."
        ". pageBody ."
        ". Footer .";
}

.pageHead {
    grid-area: pageHead;
    align-self: center;
    text-align: center;
}

.pageBody {
    grid-area: pageBody;
    text-align: center;
}

.pageFoot {
    grid-area: pageFoot;
}


